/**
 * @module jquery__config
 * @description Configuration for jQuery
 */

'use strict';

modules.define('jquery__config', function (provide) {

  provide( /** @exports */{
    /**
     * URL for loading jQuery if it does not exist
     * @type {String}
     */
    url: 'https://yastatic.net/jquery/2.1.4/jquery.min.js'
  });
});